/* View Toggle Component Styles */
.view-toggle {
  display: flex;
  align-items: center;
  gap: 1rem;
  background-color: white;
  padding: 0.5rem;
  border-radius: 12px;
  border: 2px solid var(--border-light);
  box-shadow: 0 2px 8px var(--shadow-light);
}

.view-label {
  font-weight: 600;
  color: var(--text-dark);
  font-size: 0.9rem;
  white-space: nowrap;
}

.toggle-buttons {
  display: flex;
  gap: 0.25rem;
  background-color: #f8f9fa;
  border-radius: 8px;
  padding: 0.25rem;
}

.toggle-btn {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  padding: 0.75rem 1rem;
  border: none;
  background: none;
  border-radius: 6px;
  cursor: pointer;
  font-family: inherit;
  font-size: 0.9rem;
  font-weight: 500;
  color: var(--text-light);
  transition: all 0.2s ease;
  white-space: nowrap;
}

.toggle-btn:hover {
  background-color: rgba(13, 71, 161, 0.05);
  color: var(--primary-blue);
}

.toggle-btn.active {
  background-color: var(--primary-blue);
  color: white;
  box-shadow: 0 2px 8px var(--shadow-medium);
}

.toggle-btn svg {
  width: 18px;
  height: 18px;
  transition: transform 0.2s ease;
}

.toggle-btn:hover svg {
  transform: scale(1.1);
}

.toggle-btn.active svg {
  transform: scale(1.05);
}

/* Responsive Design */
@media (max-width: 768px) {
  .view-toggle {
    flex-direction: column;
    gap: 0.75rem;
    align-items: stretch;
  }
  
  .view-label {
    text-align: center;
  }
  
  .toggle-buttons {
    justify-content: center;
  }
  
  .toggle-btn {
    flex: 1;
    justify-content: center;
    padding: 0.6rem 0.8rem;
    font-size: 0.85rem;
  }
  
  .toggle-btn span {
    display: none;
  }
  
  .toggle-btn svg {
    width: 20px;
    height: 20px;
  }
}

@media (max-width: 480px) {
  .view-toggle {
    padding: 0.4rem;
  }
  
  .toggle-buttons {
    gap: 0.2rem;
    padding: 0.2rem;
  }
  
  .toggle-btn {
    padding: 0.5rem 0.6rem;
  }
  
  .toggle-btn svg {
    width: 16px;
    height: 16px;
  }
}
