/* Mentor List Component Styles */
.mentor-list {
  display: flex;
  flex-direction: column;
  gap: 1rem;
}

.mentor-list-item {
  background: white;
  border-radius: 12px;
  border: 1px solid var(--border-light);
  box-shadow: 0 2px 12px var(--shadow-light);
  overflow: hidden;
  transition: all 0.3s ease;
}

.mentor-list-item:hover {
  transform: translateY(-2px);
  box-shadow: 0 4px 20px var(--shadow-medium);
  border-color: var(--primary-blue);
}

.list-item-header {
  display: flex;
  justify-content: space-between;
  align-items: flex-start;
  padding: 0.5rem 1.5rem;
  background: var(--gradient-primary);
  color: white;
  gap: 1rem;
}

.mentor-info {
  flex: 1;
  text-align: left;
}

.name-badge-row {
  display: flex;
  align-items: center;
  gap: 1rem;
  width: 90%;
}

.faculty-name {
  font-family: 'Montserrat', 'Arial Black', sans-serif;
  font-size: 1.3rem;
  font-weight: 700;
  margin: 0;
  line-height: 1;
  flex: 1;
  min-width: 0;
}

.university-badge {
  background: rgba(255, 255, 255, 0.85);
  color: black;
  padding: 0.1rem 0.8rem;
  border-radius: 20px;
  font-size: 0.9rem;
  font-weight: 500;
  backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.3);
  text-align: center;
  flex-shrink: 0;
  white-space: nowrap;
  max-width: fit-content;
}

/* Star Button Styles for List View */
.star-button {
  background: none;
  border: none;
  color: rgba(255, 255, 255, 0.7);
  cursor: pointer;
  border-radius: 50%;
  transition: all 0.3s ease;
  display: flex !important;
  align-items: center;
  justify-content: center;
  width: 36px;
  height: 36px;
  flex-shrink: 0;
  opacity: 1 !important;
  visibility: visible !important;
  position: static !important;
  z-index: 1 !important;
}

.star-button:hover {
  background-color: rgba(255, 255, 255, 0.1);
  color: var(--accent-gold);
  transform: scale(1.1);
}

.star-button.starred {
  color: var(--accent-gold);
}

.star-button.starred:hover {
  color: white;
  background-color: rgba(255, 193, 7, 0.2);
}

/* Ensure star button is always visible in list view */
.mentor-list .star-button,
.mentor-list-item .star-button,
.list-item-header .star-button {
  display: flex !important;
  opacity: 1 !important;
  visibility: visible !important;
  position: static !important;
  z-index: 1 !important;
}

.star-button svg {
  width: 18px;
  height: 18px;
  transition: transform 0.2s ease;
}

.star-button:hover svg {
  transform: scale(1.1);
}

.homepage-link {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  color: white;
  text-decoration: none;
  font-weight: 500;
  font-size: 0.9rem;
  padding: 0.5rem 1rem;
  background-color: rgba(255, 255, 255, 0.2);
  border-radius: 8px;
  border: 1px solid rgba(255, 255, 255, 0.3);
  transition: all 0.3s ease;
  white-space: nowrap;
  backdrop-filter: blur(10px);
  height: 36px;
  box-sizing: border-box;
}

.homepage-link:hover {
  background-color: rgba(255, 255, 255, 0.3);
  transform: translateY(-1px);
}

/* Email Link Styles for List View */
.email-link {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  color: white;
  text-decoration: none;
  font-weight: 500;
  font-size: 0.9rem;
  padding: 0.5rem 1rem;
  background-color: rgba(6, 182, 212, 0.2);
  border-radius: 8px;
  border: 1px solid rgba(6, 182, 212, 0.3);
  transition: all 0.3s ease;
  white-space: nowrap;
  backdrop-filter: blur(10px);
  height: 36px;
  box-sizing: border-box;
}

.email-link:hover {
  background-color: rgba(6, 182, 212, 0.3);
  transform: translateY(-1px);
}

.email-link svg {
  width: 16px;
  height: 16px;
}

.homepage-link svg {
  width: 16px;
  height: 16px;
}

.header-buttons {
  display: flex;
  gap: 0.75rem;
  flex-wrap: nowrap;
  align-items: center;
  height: 36px;
}

.header-buttons .homepage-link,
.header-buttons .email-link {
  flex-shrink: 0;
}

.header-buttons .star-button {
  flex-shrink: 0;
  order: 0;
  margin-right: 0;
  display: flex !important;
  opacity: 1 !important;
  visibility: visible !important;
  position: static !important;
  z-index: 1 !important;
}

.list-item-content {
  padding: 0.75rem 1rem;
  display: flex;
  flex-direction: column;
  gap: 0.1rem;
}

.info-row {
  display: flex;
  gap: 0.75rem;
  align-items: flex-start;
}

.label {
  font-weight: 600;
  color: var(--primary-blue);
  font-size: 0.9rem;
  text-transform: uppercase;
  letter-spacing: 0.5px;
  min-width: 100px;
  flex-shrink: 0;
}

.value {
  color: var(--text-dark);
  font-size: 0.95rem;
  line-height: 1.25;
  flex: 1;
}

.positions {
  color: var(--primary-blue);
  font-weight: 500;
}

.requirements {
  color: var(--text-light);
  font-style: italic;
}

.notes {
  color: var(--accent-cyan);
  font-weight: 500;
  background-color: rgba(6, 182, 212, 0.1);
  padding: 0.5rem;
  border-radius: 6px;
  border-left: 3px solid var(--accent-cyan);
}

.additional {
  color: var(--text-light);
  font-size: 0.9rem;
}

/* Responsive Design */
@media (max-width: 768px) {
  .list-item-header {
    flex-direction: column;
    align-items: stretch;
    gap: 1rem;
    padding: 1.25rem;
  }
  
  .mentor-info {
    text-align: left;
  }
  
  .star-button {
    align-self: flex-start;
  }
  
  .homepage-link {
    align-self: flex-start;
  }
  
  .header-buttons {
    flex-direction: row;
    gap: 0.5rem;
    flex-wrap: nowrap;
  }
  
  .header-buttons .homepage-link,
  .header-buttons .email-link {
    align-self: center;
    justify-content: center;
    flex-shrink: 0;
  }
  
  .header-buttons .star-button {
    order: 0;
    align-self: center;
    margin-bottom: 0;
    flex-shrink: 0;
  }
  
  .list-item-content {
    padding: 1.25rem;
    gap: 0.75rem;
  }
  
  .info-row {
    flex-direction: column;
    gap: 0.5rem;
  }
  
  .label {
    min-width: auto;
  }
}

@media (max-width: 480px) {
  .list-item-header {
    padding: 1rem;
  }
  
  .faculty-name {
    font-size: 1.1rem;
  }
  
  .university {
    font-size: 0.9rem;
  }
  
  .homepage-link {
    padding: 0.4rem 0.8rem;
    font-size: 0.85rem;
  }
  
  .list-item-content {
    padding: 1rem;
    gap: 0.5rem;
  }
  
  .label {
    font-size: 0.8rem;
  }
  
  .value {
    font-size: 0.9rem;
  }
}
