/* Mentor Card Component Styles */
.mentor-card {
  background: white;
  border-radius: 16px;
  box-shadow: 0 4px 20px var(--shadow-light);
  border: 1px solid var(--border-light);
  overflow: hidden;
  transition: all 0.3s ease;
  height: 100%;
  display: flex;
  flex-direction: column;
}

.mentor-card:hover {
  transform: translateY(-4px);
  box-shadow: 0 8px 30px var(--shadow-medium);
  border-color: var(--primary-blue);
}

.card-header {
  background: var(--gradient-primary);
  color: white;
  padding: 0.85rem;
  position: relative;
}

.header-content {
  display: flex;
  justify-content: center;
  align-items: center;
  min-height: 50px;
  width: 100%;
}

.mentor-info {
  text-align: center;
  width: 100%;
  position: relative;
  z-index: 1;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.name-star-row {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.0rem;
  width: 100%;
  margin-bottom: 0.1rem;
}

.faculty-name {
  font-family: 'Montserrat', 'Arial Black', sans-serif;
  font-size: 1.6rem;
  font-weight: 900 !important;
  margin: 0;
  line-height: 1.3;
  text-align: center;
  flex: 1;
}

.university-badge {
  background: rgba(255, 255, 255, 0.85);
  color: black;
  padding: 0.1rem 1rem;
  border-radius: 20px;
  font-size: 1rem;
  font-weight: 500;
  backdrop-filter: blur(10px);
  border: 1px solid rgba(255, 255, 255, 0.3);
  text-align: center;
  width: 100%;
  margin-bottom: 0.0rem;
}

/* Star Button Styles */
.star-button {
  background: none;
  border: none;
  color: rgba(255, 255, 255, 0.7);
  cursor: pointer;
  border-radius: 50%;
  transition: all 0.3s ease;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 36px;
  height: 36px;
  flex-shrink: 0;
  flex-grow: 0;
}

.star-button:hover {
  background-color: rgba(255, 255, 255, 0.1);
  color: var(--accent-gold);
  transform: scale(1.1);
}

.star-button.starred {
  color: var(--accent-gold);
}

.star-button.starred:hover {
  color: white;
  background-color: rgba(255, 193, 7, 0.2);
}

.star-button svg {
  width: 24px;
  height: 24px;
  transition: transform 0.2s ease;
}

.star-button:hover svg {
  transform: scale(1.1);
}

.card-content {
  padding: 1rem;
  flex: 1;
  display: flex;
  flex-direction: column;
  gap: 0.35rem;
}

.info-section h4 {
  color: var(--primary-blue);
  font-size: 0.9rem;
  font-weight: 600;
  margin: 0 0 0.1rem 0;
  text-transform: uppercase;
  letter-spacing: 0.5px;
}

.info-section p {
  margin: 0;
  color: var(--text-dark);
  font-size: 0.95rem;
  line-height: 1.2;
}

.research-interests {
  font-weight: 500;
  color: var(--text-dark);
  line-height: 1.15;
}

.positions {
  color: var(--primary-blue);
  font-weight: 500;
}

.requirements {
  color: var(--text-light);
  font-style: italic;
}

.notes {
  color: var(--accent-cyan);
  font-weight: 500;
  background-color: rgba(6, 182, 212, 0.1);
  padding: 0.5rem;
  border-radius: 6px;
  border-left: 3px solid var(--accent-cyan);
}

.additional-info {
  color: var(--text-light);
  font-size: 0.9rem;
}

.card-footer {
  padding: 0.5rem 0.5rem;
  border-top: 1px solid var(--border-light);
  background-color: #f8f9fa;
}

.footer-buttons {
  display: flex;
  gap: 0.5rem;
  flex-wrap: nowrap;
  align-items: center;
}

.footer-buttons .homepage-link,
.footer-buttons .email-link {
  flex: 1;
  min-width: 0;
}

.homepage-link {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  color: white;
  text-decoration: none;
  font-weight: 600;
  font-size: 0.9rem;
  padding: 0.75rem 1rem;
  background: var(--gradient-primary);
  border-radius: 8px;
  border: 2px solid var(--primary-blue);
  transition: all 0.3s ease;
  justify-content: center;
  box-shadow: 0 2px 8px var(--shadow-glow);
  text-shadow: 0 1px 2px rgba(0, 0, 0, 0.2);
}

.homepage-link:hover,
.homepage-link:focus {
  background: linear-gradient(135deg, #3B82F6 0%, var(--primary-blue) 100%);
  color: white;
  transform: translateY(-2px);
  box-shadow: 0 6px 20px var(--shadow-glow);
  border-color: #3B82F6;
  outline: none;
}

.homepage-link:focus {
  outline: 3px solid var(--accent-cyan);
  outline-offset: 2px;
}

/* Email Link Styles */
.email-link {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  color: white;
  text-decoration: none;
  font-weight: 600;
  font-size: 0.9rem;
  padding: 0.75rem 1rem;
  background: var(--gradient-accent);
  border-radius: 8px;
  border: 2px solid var(--accent-cyan);
  transition: all 0.3s ease;
  justify-content: center;
  box-shadow: 0 2px 8px rgba(6, 182, 212, 0.3);
  text-shadow: 0 1px 2px rgba(0, 0, 0, 0.2);
}

.email-link:hover,
.email-link:focus {
  background: linear-gradient(135deg, #3B82F6 0%, var(--accent-cyan) 100%);
  color: white;
  transform: translateY(-2px);
  box-shadow: 0 6px 20px rgba(6, 182, 212, 0.4);
  border-color: #3B82F6;
  outline: none;
}

.email-link:focus {
  outline: 3px solid var(--primary-blue);
  outline-offset: 2px;
}

.homepage-link svg {
  width: 16px;
  height: 16px;
}

/* Responsive Design */
@media (max-width: 768px) {
  .card-header {
    padding: 1.25rem;
  }
  
  .header-content {
    position: relative;
  }
  
  .mentor-info {
    /* max-width removed to ensure proper centering */
  }
  
  .star-button {
    width: 32px;
    height: 32px;
  }
  
  .star-button svg {
    width: 22px;
    height: 22px;
  }
  
  .faculty-name {
    font-size: 1.25rem;
  }
  
  .card-content {
    padding: 1.25rem;
    gap: 1rem;
  }
  
  .card-footer {
    padding: 0.75rem 1.25rem;
  }
}

@media (max-width: 480px) {
  .card-header {
    padding: 1rem;
  }
  
  .faculty-name {
    font-size: 1.1rem;
  }
  
  .university-badge {
    font-size: 0.8rem;
    padding: 0.4rem 0.8rem;
  }
  
  .card-content {
    padding: 1rem;
    gap: 0.75rem;
  }
  
  .info-section h4 {
    font-size: 0.8rem;
  }
  
  .info-section p {
    font-size: 0.9rem;
  }
  
  .card-footer {
    padding: 0.75rem 1rem;
  }
  
  .homepage-link,
  .email-link {
    padding: 0.6rem 0.8rem;
    font-size: 0.85rem;
  }
  
  .footer-buttons {
    flex-direction: column;
    gap: 0.75rem;
  }
  
  .footer-buttons .homepage-link,
  .footer-buttons .email-link {
    min-width: auto;
  }
}
