/* Global CSS Reset and Base Styles */
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

body {
  font-family: 'Poppins', 'Montserrat', -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #FFFFFF;
  color: #333333;
  line-height: 1.6;
  overflow-x: hidden;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* CSS Custom Properties for Modern Tech Colors */
:root {
  /* Modern Tech Brand Colors */
  --primary-blue: #1E40AF;
  --primary-blue-dark: #1E3A8A;
  --primary-blue-light: #3B82F6;
  --accent-cyan: #06B6D4;
  --accent-purple: #8B5CF6;
  --accent-emerald: #10B981;
  --accent-gold: #FFC107;
  --background-white: #FFFFFF;
  --background-gray: #F8FAFC;
  --background-dark: #0F172A;
  
  /* Text Colors */
  --text-dark: #1E293B;
  --text-light: #64748B;
  --text-muted: #94A3B8;
  --text-white: #F8FAFC;
  
  /* Border Colors */
  --border-light: #E2E8F0;
  --border-medium: #CBD5E1;
  --border-dark: #94A3B8;
  --border-accent: #3B82F6;
  
  /* Shadow Colors */
  --shadow-light: rgba(15, 23, 42, 0.08);
  --shadow-medium: rgba(15, 23, 42, 0.12);
  --shadow-dark: rgba(15, 23, 42, 0.16);
  --shadow-glow: rgba(59, 130, 246, 0.15);
  
  /* Status Colors */
  --success-green: #10B981;
  --warning-orange: #F59E0B;
  --error-red: #EF4444;
  --info-blue: #3B82F6;
  
  /* Gradient Colors */
  --gradient-primary: linear-gradient(135deg, #1E40AF 0%, #3B82F6 100%);
  --gradient-accent: linear-gradient(135deg, #06B6D4 0%, #3B82F6 100%);
  --gradient-gold: linear-gradient(135deg, #f6d365 0%, #fda085 100%);
  --gradient-purple: linear-gradient(135deg, #8B5CF6 0%, #A855F7 100%);
  --gradient-success: linear-gradient(135deg, #10B981 0%, #34D399 100%);
}

/* Utility Classes */
.container {
  max-width: 1400px;
  margin: 0 auto;
  padding: 0 20px;
}

.text-center {
  text-align: center;
}

.mb-1 { margin-bottom: 0.5rem; }
.mb-2 { margin-bottom: 1rem; }
.mb-3 { margin-bottom: 1.5rem; }
.mb-4 { margin-bottom: 2rem; }

.mt-1 { margin-top: 0.5rem; }
.mt-2 { margin-top: 1rem; }
.mt-3 { margin-top: 1.5rem; }
.mt-4 { margin-top: 2rem; }

/* Responsive Design */
@media (max-width: 768px) {
  .container {
    padding: 0 15px;
  }
}
