/* Tab Navigation Component Styles */
.tab-navigation {
  display: flex;
  background-color: white;
  border-radius: 12px;
  padding: 0.5rem;
  border: 2px solid var(--border-light);
  box-shadow: 0 2px 8px var(--shadow-light);
  margin-bottom: 1rem;
  gap: 0.25rem;
}

.tab-button {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  padding: 0.75rem 1.5rem;
  border: none;
  background: none;
  border-radius: 8px;
  cursor: pointer;
  font-family: inherit;
  font-size: 0.95rem;
  font-weight: 500;
  color: var(--text-light);
  transition: all 0.3s ease;
  flex: 1;
  justify-content: center;
  white-space: nowrap;
}

.tab-button:hover {
  background-color: rgba(13, 71, 161, 0.05);
  color: var(--primary-blue);
}

.tab-button.active {
  background-color: var(--primary-blue);
  color: white;
  box-shadow: 0 2px 8px var(--shadow-medium);
}

.tab-button svg {
  width: 18px;
  height: 18px;
  transition: transform 0.2s ease;
}

.tab-button:hover svg {
  transform: scale(1.1);
}

.tab-button.active svg {
  transform: scale(1.05);
}

/* Responsive Design */
@media (max-width: 768px) {
  .tab-navigation {
    margin-bottom: 1.5rem;
  }
  
  .tab-button {
    padding: 0.6rem 1rem;
    font-size: 0.9rem;
  }
  
  .tab-button svg {
    width: 16px;
    height: 16px;
  }
}

@media (max-width: 480px) {
  .tab-navigation {
    padding: 0.4rem;
    margin-bottom: 1rem;
  }
  
  .tab-button {
    padding: 0.5rem 0.8rem;
    font-size: 0.85rem;
    gap: 0.3rem;
  }
  
  .tab-button svg {
    width: 14px;
    height: 14px;
  }
}
