/* Main App Layout */
.App {
  min-height: 100vh;
  background-color: transparent;
  position: relative;
  z-index: 1;
}

.main-content {
  padding: 2rem 0;
}

/* Search Section */
.search-section {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1rem;
  gap: 1rem;
  flex-wrap: wrap;
}

/* Results Info */
.results-info {
  margin-bottom: 1rem;
  padding: 0.75rem;
  background-color: #f8f9fa;
  border-radius: 8px;
  border-left: 4px solid var(--primary-blue);
}

.results-info p {
  margin: 0;
  color: var(--text-light);
  font-weight: 500;
}

/* Mentors Container */
.mentors-container {
  margin-top: 1rem;
}

/* Mentors Grid (Card View) */
.mentors-grid {
  display: grid;
  grid-template-columns: repeat(auto-fill, minmax(250px, 1fr));
  gap: 2rem;
  margin-bottom: 2rem;
}

/* No Results */
.no-results {
  text-align: center;
  padding: 3rem;
  background-color: #f8f9fa;
  border-radius: 8px;
  border: 2px dashed var(--border-light);
}

.no-results p {
  margin: 0.5rem 0;
  color: var(--text-light);
}

.no-results p:first-child {
  font-size: 1.2rem;
  font-weight: 600;
  color: var(--text-dark);
}

/* Loading States */
.loading-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  gap: 1rem;
}

.loading-spinner {
  width: 50px;
  height: 50px;
  border: 4px solid var(--border-light);
  border-top: 4px solid var(--primary-blue);
  border-radius: 50%;
  animation: spin 1s linear infinite;
}

@keyframes spin {
  0% { transform: rotate(0deg); }
  100% { transform: rotate(360deg); }
}

/* Responsive Design */
@media (max-width: 768px) {
  .main-content {
    padding: 1rem 0;
  }
  
  .search-section {
    flex-direction: column;
    align-items: stretch;
  }
  
  .mentors-grid {
    grid-template-columns: 1fr;
    gap: 1rem;
  }
  
  .results-info {
    margin-bottom: 1rem;
  }
}

@media (max-width: 480px) {
  .container {
    padding: 0 10px;
  }
  
  .mentors-grid {
    gap: 0.5rem;
  }
}
