/* Search Bar Component Styles */
.search-bar {
  flex: 1;
  max-width: 800px;
}

.search-input-wrapper {
  position: relative;
  display: flex;
  align-items: center;
}

.search-icon {
  position: absolute;
  left: 16px;
  width: 20px;
  height: 20px;
  color: var(--text-light);
  z-index: 2;
}

.search-input {
  width: 100%;
  padding: 16px 16px 16px 48px;
  border: 2px solid var(--border-light);
  border-radius: 12px;
  font-size: 1rem;
  font-family: inherit;
  background-color: white;
  transition: all 0.3s ease;
  box-shadow: 0 2px 8px var(--shadow-light);
}

.search-input:focus {
  outline: none;
  border-color: var(--primary-blue);
  box-shadow: 0 4px 16px var(--shadow-medium);
  transform: translateY(-1px);
}

.search-input::placeholder {
  color: var(--text-light);
  font-weight: 400;
}

.clear-button {
  position: absolute;
  right: 12px;
  background: none;
  border: none;
  cursor: pointer;
  padding: 8px;
  border-radius: 50%;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.2s ease;
  color: var(--text-light);
}

.clear-button:hover {
  background-color: var(--border-light);
  color: var(--text-dark);
}

.clear-button svg {
  width: 16px;
  height: 16px;
}

/* Responsive Design */
@media (max-width: 768px) {
  .search-bar {
    max-width: none;
  }
  
  .search-input {
    padding: 14px 14px 14px 44px;
    font-size: 0.95rem;
  }
  
  .search-icon {
    left: 14px;
    width: 18px;
    height: 18px;
  }
}

@media (max-width: 480px) {
  .search-input {
    padding: 12px 12px 12px 40px;
    font-size: 0.9rem;
    border-radius: 8px;
  }
  
  .search-icon {
    left: 12px;
    width: 16px;
    height: 16px;
  }
  
  .clear-button {
    right: 8px;
    padding: 6px;
  }
  
  .clear-button svg {
    width: 14px;
    height: 14px;
  }
}
